import React from 'react'
import { Box,Stack,Typography } from '@mui/material'
import Loader from './Loader';

const ExerciseVideos = ({exerciseVideos,name}) => {

  if (!exerciseVideos.length) return <Loader />;

  return (
    <Box sx={{ marginTop: { lg: '203px', xs: '20px' } }} p="20px">
      <Typography sx={{ fontSize: { lg: '44px', xs: '25px' } }} fontWeight={700} color="#000" mb="33px">
        <span style={{ color: '#F45050', textTransform: 'capitalize' }}>{name}</span> Exercise Tutorial Videos
      </Typography>
      <Stack sx={{ flexDirection: { lg: 'row' }, gap: { lg: '110px', xs: '0px' } }} justifyContent="flex-start" flexWrap="wrap" alignItems="center">
        {exerciseVideos?.slice(0,3).map((item, index) =>{
          return <a  
          key={index}
          className="exercise-video"
          href={`https://www.youtube.com/watch?v=${item.video.videoId}`}
          target="_blank"
          rel="noreferrer">
            <img style={{ borderRadius: '20px' }} src={item.video.thumbnails[0].url} alt={item.video.title} />
            <Box>
              <Typography sx={{ fontSize: { lg: '28px', xs: '18px' } }} fontWeight={600} color="#000">
                {item.video.title}
              </Typography>
              <Typography fontSize="14px" color="#3C486B">
                {item.video.channelName}
              </Typography>
            </Box>
          </a>
        })}
      </Stack>
    </Box>
  )
}

export default ExerciseVideos